import { mapActions, mapState } from 'vuex'

export default {
  props: {
    inputs: {
      type: Array,
      default: () => []
    },
    values: {
      type: Object,
      default: () => ({})
    },
    type: {
      type: String,
      default: ''
    },
    doInit: {
      type: Boolean,
      default: false
    },
    doStore: {
      type: Boolean,
      default: true
    },
    end: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapState({
      loaded: state => state.isLoaded
    })
  },
  methods: {
    ...mapActions({
      init: 'report/init',
      save: 'report/save',
      store: 'report/store'
    }),
    async submit (data) {
      this.$nuxt.$loading.start()
      if (this.doInit) {
        await this.init(this.type)
      }
      await this.save(data)
      if (this.doStore) {
        await this.store()
      }
      this.$router.push(this.end)
      this.$nuxt.$loading.finish()
    }
  }
}
